import React from "react";
import { Menu, Icon } from 'antd';
const { SubMenu } = Menu;

export default class ADNavMenu extends React.Component {
  state = {
    current: 'mail',
  };
  // 选择菜单
  handleClick = e => {
    console.log('click ', e);
    this.setState({
      current: e.key,
    });
  };
  render() {
    return (
      /* 
         selectedKeys：当前选中的菜单 ['mail']
         mode：菜单类型,水平horizontal 垂直vertical 内联inline
      */
      <div className="nav-menu">
        <Menu
          onClick={this.handleClick}
          selectedKeys={[this.state.current]}
          mode="horizontal"
          triggerSubMenuAction="click"
          theme="dark"
        >
          <Menu.Item key="document">
            <a
              href="#/document"
              rel="noopener noreferrer"
            >
              返回文档
            </a>
          </Menu.Item>
          <Menu.Item key="app" disabled>
            <Icon type="appstore" />
            Navigation Two
          </Menu.Item>
          <SubMenu
            title={
              <span className="submenu-title-wrapper">
                <Icon type="setting" />
                Navigation Three - Submenu
              </span>
            }
            popupClassName="nav-common-sub"
          >
            <Menu.ItemGroup title={<div className="line"><span className="text">Item 1</span></div>}>
              <Menu.Item key="setting:1">Option 1</Menu.Item>
              <Menu.Item key="setting:2">Option 2</Menu.Item>
            </Menu.ItemGroup>
            <Menu.ItemGroup title={<div className="line"><span className="text">Item 2</span></div>}>
              <Menu.Item key="setting:3">Option 3</Menu.Item>
              <Menu.Item key="setting:4">Option 4</Menu.Item>
            </Menu.ItemGroup>
          </SubMenu>
          <Menu.Item key="alipay">
            <a
              href="https://ant.design"
              target="_blank"
              rel="noopener noreferrer"
            >
              Navigation Four - Link
            </a>
          </Menu.Item>
        </Menu>
      </div>
    );
  }
}
