import React from "react";
import RenderMenu from './render-menu'
import { Menu } from "antd";

export default class ADLeftMenu extends React.Component {
  state = {
    current: "mail",
  };
  // 选择菜单
  handleClick = e => {
    console.log("click ", e);
    this.setState({
      current: e.key
    });
  };
  render() {
    let { menu } = this.props
    return (
      /* 
         selectedKeys：当前选中的菜单 ['mail']
         mode：菜单类型,水平horizontal 垂直vertical 内联inline
      */
      menu.length>0&&<div className="left-menu">
      <Menu
        onClick={this.handleClick}
        selectedKeys={[this.state.current]}
        theme="dark"
        mode="inline"
        className="inline-menu"
      >
        { RenderMenu(menu) }
      </Menu>
    </div>
    );
  }
}
