import React from 'react';
import { Link } from 'react-router-dom'

export default class ADLogo extends React.Component {
    Logo =require("@/assets/fan-ant-design/fan-logo.svg")
    render() {
        // console.log('home', this.props )
        return (
            <Link className="logo" to="/ant-design">
                <img alt="logo" src={this.Logo} />
                <span>Fan Ant Design</span>
            </Link>
        )
    }
}