import React from "react";
import { connect } from "react-redux";
import Header from "./view/header";
import Main from "./view/main";
import { Row, Col } from "antd";
import { Scrollbar } from "@/components";
import "./index.scss";
class AntDesign extends React.Component {
  state = {
    menu: [
      {
        title: "group1",
        key: "group1",
        menuList: [
          {
            title: "文档",
            key: "document",
            children: [
              { title: "文档1", key: "document1" },
              { title: "文档2", key: "document2" }
            ]
          },
          {
            title: "新闻1",
            key: "new1"
          }
        ]
      },
      {
        title: "多级菜单",
        key: "menu",
        menuList: [
          {
            title: "菜单1",
            key: "menu1",
            children: [
              {
                title: "新闻2-1",
                key: "new2-1",
                group: { title: "group2-1", key: "group2-1" },
                children: [
                  {
                    title: "新闻2-1-1",
                    key: "新闻2-1-1"
                  },
                  {
                    title: "新闻2-1-2",
                    key: "新闻2-1-2"
                  }
                ]
              },
              {
                title: "新闻2-2",
                key: "new2-2",
                group: { title: "group2-2", key: "group2-2" }
              }
            ]
          },
          {
            title: "菜单2",
            key: "menu2"
          }
        ]
      }
    ]
  };
  render() {
    // console.log('home', this.props )
    return (
      <div className="antd-page">
        <div className="antd-header">
          <Row>
            <Col span={5}>
              <div className="antd-header-left">
                <Header.Logo />
              </div>
            </Col>
            <Col span={19}>
              <div className="antd-header-right clearfix">
                <Row>
                  <Col span={6}>
                    <Header.Search />
                  </Col>
                  <Col span={18}>
                    <Header.Nav />
                  </Col>
                </Row>
              </div>
            </Col>
          </Row>
        </div>
        <div className="antd-main-warp">
          <Row type="flex">
            {this.state.menu.length > 0 && (
              <Col span={4}>
                <div className="antd-main-warp-left">
                  {/* options={{
                      // Disabled horizontal scrolling, https://github.com/utatti/perfect-scrollbar#options
                      suppressScrollX: true
                    }} */}
                  <Scrollbar
                    component="div"                    
                  >
                    <Main.LeftMenu menu={this.state.menu} />
                  </Scrollbar>
                </div>
              </Col>
            )}
            <Col span={this.state.menu.length > 0 ? 20 : 24}>
              <div className="antd-main-warp-right"></div>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}
const mapStateToProps = state => {
  // console.log('state',state) //redux store值
  return {
    userInfo: state.userInfo
  };
};

export default connect(mapStateToProps)(AntDesign);
