//3.1 index1.js
import React from "react";
import { HashRouter, Switch, Route } from "react-router-dom";
import App from "../../App"; //引入组件
import Document from "../../views/Document";
import ReactKnowledge from "../../views/Wirkung/view/p_react_knowledge"; //React知识
import { react_knowledge_example } from "./ReactKnowledgeDemo";

const router = [
  {
    path: "/document",
    component: Document,
    exact: true
  },
  {
    path: "/wirkung",
    component: Wirkung,
    routes: [ //routes为子路由
      {
        path: "/wirkung/to_markdown_edit", //markdown编辑器
        component: MarkdownEdit,
        exact: true
      },
      {
        path: "/wirkung/to_reactknowledge", //react知识点
        component: ReactKnowledge,
        routes: [
          {
            path: "/wirkung/to_reactknowledge/to_reactknowledge_demo1",
            component: react_knowledge_example.Demo1,
            exact: true
          },
          {
            path: "/wirkung/to_reactknowledge/to_reactknowledge_demo2",
            component: react_knowledge_example.Demo2,
            exact: true
          },          
        ]
      },
    ]
  },
]

const BasicRoute = () => (
  <HashRouter>    
      <Switch>
        <Route path="/login" component={App} exact></Route> //设置单独一个路由来重定向
        <Route path="/" component={Home} ></Route>
      </Switch>
  </HashRouter>
);

export default BasicRoute;

export{
  router
}
