export default class PAntDesignDemo1 extends React.Component {
    render() {
        const markConfig={
          markConfig:{  
            md:false,
            menu:false,
            toolbar_show:false,
            height:`100%`           
          },
          markContent: content
        }
        return (
            <div>              
                <div style={{height:'467px'}}>
                   <h2 className="h2_common">AntDesign-安装及使用</h2>
                   <MarkDown {...markConfig}/>
                </div>
            </div>
        )
    }
}